@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  background-color: #282c34;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar{
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.loader{
    color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0;
    margin: 0;
    font-family: 'Roboto Mono', monospace;
}

p{
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 0.8rem;
}

.iicjec{
    font-size: 2rem;
    letter-spacing: 1rem;
    text-align: center;
}

.loader-content{
    display: flex;
    flex-direction: column;
}
.image-container img{
    width: 400px;
}

.image-item1{
    position: absolute;
    top: 10%;
    left: 5%;
}
.image-item2{
    position: absolute;
    top: 10%;
    left: 65%;
}
.image-item3{
    position: absolute;
    top: 15%;
    left: 35%;
}
.image-item4{
    position: absolute;
    top: 50%;
    left: 5%;
}
.image-item5{
    position: absolute;
    top: 50%;
    left: 65%;
}
.image-item6{
    position: absolute;
    top: 55%;
    left: 35%;
}


@media (min-width: 600px) and (max-width: 1024px) {
    .image-container img{
        width: 250px;
    }
  }
@media (max-width: 399px) {
    .image-container img{
        width: 150px;
    }
    .image-item1{
        position: absolute;
        top: 10%;
        left: 5%;
    }
    .image-item2{
        position: absolute;
        top: 10%;
        left: 55%;
    }
    .image-item3{
        position: absolute;
        top: 30%;
        left: 30%;
    }
    .image-item4{
        position: absolute;
        top: 50%;
        left: 5%;
    }
    .image-item5{
        position: absolute;
        top: 50%;
        left: 55%;
    }
    .image-item6{
        position: absolute;
        top: 70%;
        left: 30%;
    }
  }
@media (min-width: 400px) and (max-width: 599px) {
    .image-container img{
        width: 150px;
    }
    .image-item1{
        position: absolute;
        top: 10%;
        left: 5%;
    }
    .image-item2{
        position: absolute;
        top: 10%;
        left: 60%;
    }
    .image-item3{
        position: absolute;
        top: 30%;
        left: 35%;
    }
    .image-item4{
        position: absolute;
        top: 50%;
        left: 5%;
    }
    .image-item5{
        position: absolute;
        top: 50%;
        left: 60%;
    }
    .image-item6{
        position: absolute;
        top: 70%;
        left: 35%;
    }
  }
